export default function KeyboardEventDemo(){
    function handleKeyPress(e){
        console.log("Key Pressed",e.key);
        if(e.key === "Enter"){
            alert("You pressed Enter");
        }
    }
    return (
        <div>
            <h2>Keyboard Event Demo</h2>
            <input type="text" placeholder="Press any key" onKeyDown={handleKeyPress}/>
        </div>
    )
}