import { useState } from "react";

export default function BuggyComponent(){
    const[count,setCount] = useState(0);

    if(count === 5){
        throw new Error("I crashed");
    }

    return (<div>
        <h2>Buggy Counter: {count}</h2>
        <button onClick={()=>setCount(count+1)}>Increment</button>
    </div>)
}