import { useEffect, useRef } from "react";

export default function NativeVsSynthetic() {
  const btnRef = useRef();
  useEffect(() => {
    btnRef.current.addEventListener("click", () =>
      console.log("Native DOM Event clicked")
    );
  }, []);

  function handleReactClick() {
    console.log("React Synhetic Event Clicked");
  }
  return (
    <div>
      <h2>⚡ Native vs Synthetic Events</h2>
      <button ref={btnRef} onClick={handleReactClick}>Click Me</button>
    </div>
  );
}
