import { Component } from "react";

class LifeCycleDemo extends Component{
    constructor(props){
        super(props);
        console.log("Constructor: Initialize state");
        this.state = {count: 0};
    }

    componentDidMount(){
        console.log("componentDidMount: Runs after first render");

    }
    componentDidUpdate(prevProps,prevState){
            console.log("componentDidUpdate: Runs after re-render");

    }
    componentWillUnmount(){
        console.log("componentWillUnmount: Cleanup before removal");
    }

    render(){
        console.log("Life cycle method");
        return(
            <button onClick={() => this.setState({count: this.state.count + 1})}>Count: {this.state.count}</button>
        )
    }
}

export default LifeCycleDemo;