import { useState } from "react";
import LifeCycleDemo from "./LifeCycleDemoClass";
import LifeCycleDemoHook from "./LifeCycleDemoHook";

export default function AppLifeCycleDemo() {
  const [show, setShow] = useState(true);
  return (
    <div>
      <button onClick={() => setShow(!show)}>Toggle Component</button>
      {show && (
        <>
          <LifeCycleDemo /> 
          <LifeCycleDemoHook />
        </>
      )}
    </div>
  );
}
